<template>
  <section id="skill-section">
    <h1 class="section-title">SKILL</h1>
    <div ref="box_animation">
      <ul class="box-wrap">
        <li :class="['box', {'active':item.active}]" :style="`background-color: ${item.color}`" v-for="(item, index) in skillList" :key="index" @click="clickBox(index)">
          <div class="info">
            <ul>
              <li class="mb6" v-for="(subItem, idx) in item.items" :key="idx">
                <div class="icon">
                  <img :src="subItem.icon">
                </div>
                <div>
                  <p class="title pl6">{{ subItem.title }}</p>
                  <p class="contents">{{ subItem.contents }}</p>
                </div>
              </li>
            </ul>
          </div>
          <div class="shadow"></div>
          <div class="label">
            <div class="icon">
              <img :src="item.icon" />
            </div>
            <div class="main-title">{{ item.title }}</div>
          </div>
        </li>
      </ul>
    </div>
    <div class="scroll"></div>
  </section>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';
// item icon
import html_icon from '@/assets/images/skill/html.png';
import css_icon from '@/assets/images/skill/css.png';
import js_icon from '@/assets/images/skill/vanila.png';
// import server_icon from '@/assets/images/skill/server.svg';
// import build_icon from '@/assets/images/skill/build.png';
// sub item icon
import php_icon from '@/assets/images/skill/php.png';
import tailwind_icon from '@/assets/images/skill/tailwind.png';
import scss_icon from '@/assets/images/skill/scss.png';
import ts_icon from '@/assets/images/skill/ts.png';
import vue_icon from '@/assets/images/skill/vue.png';
import vuex_icon from '@/assets/images/skill/vuex.png';
// import node_icon from '@/assets/images/skill/node.png';
import jQuery_icon from '@/assets/images/skill/jQuery.png';
import pinia_icon from '@/assets/images/skill/pinia.png';
// import graphQL_icon from '@/assets/images/skill/graphQL.png';
// import vite_icon from '@/assets/images/skill/vite.svg';

export default {
  setup() {
    const skillList = ref ([
      {
        title: 'HTML',
        icon: html_icon,
        color: '#ffe7de',
        active: true,
        items: [
          {
            title: 'HTML',
            icon: html_icon,
            contents: '웹표준을 준수하고, 웹 접근성을 고려하여 마크업합니다.',
          },
          {
            title: 'PHP',
            icon: php_icon,
            contents: 'Cafe24기반 PHP 홈페이지 유지보수 경험이 있습니다.',
          },
        ],
      },
      {
        title: 'CSS',
        icon: css_icon,
        color: '#fef0d7',
        active: false,
        items: [
          {
            title: 'CSS',
            icon: css_icon,
            contents: '크로스 브라우징 경험이 있으며, 시안에 따라 적절한 레이아웃을 구현할 수 있습니다. 미디어 쿼리를 활용하여 반응형 웹을 구현합니다.',
          },
          {
            title: 'SCSS',
            icon: scss_icon,
            contents: '문법을 적절하게 적용 할 수 있습니다.',
          },
          {
            title: 'Tailwind',
            icon: tailwind_icon,
            contents: '문서를 보며 사용할 수 있습니다.',
          },
        ],
      },
      {
        title: 'JavaScript',
        icon: js_icon,
        color: '#e5f3da',
        active: false,
        items: [
          {
            title: 'JavaScript',
            icon: js_icon,
            contents: 'ES6 문법 숙련, Vanila JavaScript로 기능을 구현할 수 있습니다.',
          },
          {
            title: 'TypeScript',
            icon: ts_icon,
            contents: '문서를 보며 사용할 수 있습니다. 짧은 기간동안 사용 경험이 있습니다.',
          },
          {
            title: 'Vue',
            icon: vue_icon,
            contents: 'Vue2 option api Vue3 composition api 경험이 있습니다.',
          },
          {
            title: 'Vuex',
            icon: vuex_icon,
            contents: '상태관리의 흐름을 알고 있으며 적절하게 사용합니다.',
          },
          {
            title: 'pinia',
            icon: pinia_icon,
            contents: '문서를 보며 사용할 수 있습니다.',
          },
          {
            title: 'jQuery',
            icon: jQuery_icon,
            contents: 'jQuery가 사용된 프로젝트 유지보수 경험이 있습니다.',
          },
        ],
      },
      // {
      //   title: 'Query',
      //   icon: server_icon,
      //   color: '#d3dbea',
      //   active: false,
      //   items: [
      //     {
      //       title: 'graphQL',
      //       icon: graphQL_icon,
      //       contents: '프론트 관점에서 사용할 수 있습니다.',
      //     },
      //   ],
      // },
      // {
      //   title: 'Build',
      //   icon: build_icon,
      //   color: '#e8e6f1',
      //   active: false,
      //   items: [
      //     {
      //       title: 'vite',
      //       icon: vite_icon,
      //       contents: '문서를 보며 사용할 수 있습니다.',
      //     },
      //   ],
      // },
    ]);

    //  **** 클릭 이벤트 ****
    const clickBox = (index) => {
      skillList.value.forEach(item => item.active = false);
      skillList.value[index].active = true;
    }

    //  **** 휠 이벤트 ****
    let progress = 0;
    let active = 0;
    let speedWheel = 0.2;
    const handleWheel = e => {
      const wheelProgress = e.deltaY * speedWheel
      progress = Math.max(0, Math.min(progress + wheelProgress, 100));
      active = Math.floor(progress/100*(skillList.value.length-1));

      skillList.value.forEach((item, index) => {
        if (active === index) item.active = true;
        else item.active = false
      });
    };

    //  **** 애니메이션 ****
    const box_animation = ref();

    onMounted(() => {
      document.addEventListener('mousewheel', handleWheel);
      setTimeout(() => {
        box_animation.value.style.opacity = 1;
      }, 200);
    });

    onUnmounted(() => {
      document.removeEventListener('mousewheel', handleWheel);
    });

    return {
      box_animation,
      skillList,
      clickBox,
    };
  }
}
</script>